/* src/styles/Register.css */

.register-wrapper {
    height: 100vh;
    background: linear-gradient(to right, #d4fc79, #fdfc47, #f2709c);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .register-form {
    width: 350px;
    padding: 2rem;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  }
  
  .form-title {
    text-align: center;
    color: #9c5700;
    margin-bottom: 1rem;
    font-weight: 600;
  }
  
  .form-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .register-btn {
    background-color: #a36b00;
    border: none;
    padding: 8px 18px;
    font-size: 15px;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }
  
  .register-btn:hover {
    background-color: #874f00;
  }
  
  .link {
    font-size: 12px;
    color: #444;
  }
  