.login-container {
        display: flex;
        min-height: 100vh;
        background-color: #2b65bb; /* ðŸ‘ˆ Light gray background */
      }
      .login-btn {
        padding: 12px 28px;
        font-size: 16px;
        font-weight: 500;
      }
        
  
  
  .login-left {
    flex: 1;
    background-color: #c5b5c1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .login-left img {
    max-width: 100%;
    height: auto;
    padding: 20px;
  }
  
  .login-right {
    flex: 1;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(245, 210, 210);
  }
  
  .login-right h2 {
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 40px;
    
    
  }
  